html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  overflow-x: hidden;
  font-family: $u;
}
body,
html {
  font-size: 10px;
}
body.no-scroll,
html.no-scroll {
  overflow: hidden;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
*:active,
*:focus {
  outline: 0;
}
b,
strong {
  font-weight: 700;
}
a {
  text-decoration: none;
  color: inherit;
}
a:link,
a:visited,
a:active {
  text-decoration: none;
}
img {
  max-width: 100%;
}
@media screen and (max-width: 997px) {
  img {
    object-fit: cover;
  }
}
button {
  cursor: pointer;
}
input,
select,
textarea {
  padding: 5px 10px;
  font-family: inherit;
  resize: none;
}
pre {
  font-size: 16px;
  display: block;
  text-align: left;
  background: #fafafa;
  border: 2px solid #e1e1e1;
  border-radius: 5px;
  padding: 20px;
  line-height: 28px;
  font-family: monospace;
  overflow: scroll;
}
.center-content {
  width: 100%;
  margin: auto;
  z-index: 2;
  margin-left: auto;
  margin-right: auto;
}
@media screen and (min-width: 998px) and (max-width: 1279px) {
  .center-content {
    padding: 0 10px;
  }
}
.center-content:not(.not-relative) {
  position: relative;
}
@media screen and (max-width: 997px) {
  .center-content:not(.nopadding) {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (min-width: 997px) {
  .center-content:not(.bigger) {
    width: 1240px;
  }
}
@media screen and (min-width: 998px) and (max-width: 1279px) {
  .center-content:not(.bigger) {
    width: 1000px !important;
  }
}
@media screen and (min-width: 997px) {
  .center-content.bigger {
    width: 1240px;
  }
}
@media screen and (min-width: 1500px) {
  .center-content.bigger {
    width: 1500px;
  }
}
@media screen and (min-width: 998px) and (max-width: 1279px) {
  .center-content.bigger {
    width: 1000px !important;
  }
}
@media screen and (max-width: 997px) {
  .center-content:not(.unlimited) {
    max-width: 320px;
  }
}
@media screen and (max-width: 997px) {
  .center-content .center-content {
    padding: 0 !important;
  }
}
.full {
  display: block;
  width: 100%;
}
@media screen and (min-width: 997px) {
  .flexed {
    display: flex;
  }
}
@media screen and (min-width: 997px) {
  .row {
    flex-flow: row wrap;
  }
}
@media screen and (min-width: 997px) {
  .spaced {
    justify-content: space-between;
  }
}
@media screen and (min-width: 997px) {
  .centered {
    align-items: center;
  }
}
@media screen and (min-width: 997px) {
  .start {
    align-items: flex-start;
  }
}
.dibfather {
  font-size: 0;
}
.dib,
.icon-after:after {
  display: inline-block;
  vertical-align: top;
}
.cleared:after {
  content: '';
  display: block;
  clear: both;
}
.transitioned-basic {
  transition: all 0.2s ease-in-out;
}
@media screen and (max-width: 997px) {
  .hideonmobile {
    display: none;
  }
}
@media screen and (max-width: 997px) {
  .becomes-carousel-on-mobile .slick-slide {
    margin: 0 40px;
  }
  .becomes-carousel-on-mobile .slick-list {
    margin: 0 -40px;
  }
}
.icon-after:after {
  content: '';
  font-family: $fa;
  font-weight: 700;
}
@media screen and (min-width: 997px) {
  .mobile-only {
    display: none;
  }
}
.has-low-border {
  position: relative;
  padding-bottom: 13px;
}
.has-low-border:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  width: 90%;
  background-color: $regularBlue;
  height: 2px;
}
.has-low-border:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  height: 3px;
  background-color: $regularBlue;
  width: 84px;
}
@media screen and (max-width: 997px) {
  .has-low-border:before {
    bottom: -2px;
  }
}
@media screen and (min-width: 997px) {
  .has-low-border:before {
    bottom: -3px;
  }
}
.has-low-border.smaller-border {
  padding-bottom: 8px;
}
.has-low-border.smaller-border:after {
  width: 85%;
}
.dotted-blue li {
  font-size: inherit;
  line-height: inherit;
  color: inherit;
}
.dotted-blue li:before {
  content: 'â€¢';
  margin-right: 3px;
  color: $lighterBlue;
  line-height: inherit;
}
.block {
  display: block;
}
@media screen and (min-width: 997px) {
  .mobile-only {
    display: none;
  }
}
.full {
  display: block;
  width: 100%;
  clear: both;
}
@media screen and (min-width: 997px) {
  .half,
  .half-child {
    width: 47.5%;
  }
}
.half-child + .half-child:not(:nth-child(2n+3)) {
  margin-left: 5%;
}
@media screen and (min-width: 997px) {
  .one-third,
  .one-third-child {
    width: 31.66%;
  }
}
.one-third-child + .one-third-child:not(:nth-of-type(3n+4)) {
  margin-left: 2.5%;
}
@media screen and (min-width: 997px) {
  .one-fourth,
  .one-fourth-child {
    width: 23.75%;
  }
}
@media screen and (min-width: 997px) {
  .one-fourth-child + .one-fourth-child:not(:nth-of-type(4n+5)) {
    margin-left: 1.666%;
  }
}
@media screen and (min-width: 997px) {
  .one-fifth,
  .one-fifth-child {
    width: 18%;
  }
}
@media screen and (min-width: 997px) {
  .one-fifth-child + .one-fifth-child:not(:nth-of-type(5n+6)) {
    margin-left: 2.5%;
  }
}
@media screen and (min-width: 997px) {
  .two-thirds {
    width: 63.334%;
  }
}
@media screen and (min-width: 997px) {
  .two-thirds:last-child {
    margin-left: 5%;
  }
}
